input[type='checkbox'] {
	clip: rect(0 0 0 0);
	clip-path: inset(50%);
	height: 1px;
	overflow: hidden;
	position: absolute;
	white-space: nowrap;
	width: 1px;
}

.checkbox {
	display: inline-block;
	height: 20px;
	width: 20px;
	background: #fff;
	border: 2px #ddd solid;
	border-radius: 20px;
	margin-left: 1rem;
}
